import { Store, StoreOptions } from "vuex";
import { state } from "./state";
import { getters } from "./getters";
import { mutations } from "./mutations";
import { actions } from "./actions";
import { throttle } from "lodash";
// import generalState from "@front/store/general/general";
const changeDetected = throttle(function(...args) {
	console.log("changed!", ...args);
}, 1000);
const reactive: any = (obj: any) => {
	if (typeof obj !== "object" || obj === null) {
		return obj;
	}
	return new Proxy(obj, {
		get: function(target: any, prop: any) {
			const descriptor = Object.getOwnPropertyDescriptor(target, prop);
			if (descriptor) {
				descriptor.configurable = true;
			}
			return reactive(target[prop]);
		},
		set: function(target: any, prop: any, value: any): any {
			changeDetected(prop);
			target[prop] = value;
			return target[prop] === value;
		},
		apply(target, thisArg, args) {
			changeDetected([...thisArg, ...args]);
			return target.apply(thisArg, args);
		}
	});
};

// const proxyState = reactive(state);

const store = new Store({
	state,
	getters,
	mutations,
	actions
});

export default reactive(store);
